body {
  background-color: #eff0f0;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  overflow: hidden;
}
#root {
  width: 100%;
  height: 100%;
}
.App {
  width: 100%;
  height: 100%;
  margin: auto;
}
.standard-button {
  border-radius: 10px;
  border-color: rgb(35, 179, 204);
  background-color: rgb(20, 160, 216);
  color: white;
  cursor: pointer;
  -webkit-box-shadow: 1px 1px #5f5f5f;
          box-shadow: 1px 1px #5f5f5f;
  outline: 0;
}
.delete-button {
  border-radius: 10px;
  border-color: rgb(196, 58, 58);
  background-color: rgb(255, 71, 71);
  color: white;
  cursor: pointer;
  -webkit-box-shadow: 1px 1px #5f5f5f;
          box-shadow: 1px 1px #5f5f5f;
  outline: 0;
}
.bold {
  font-weight: bold;
}



