.new-assignment.container {
  height: 85%;
  width: 96%;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;

  -webkit-box-sizing: border-box;

          box-sizing: border-box;
  border: 2px solid rgb(194, 194, 194);
  border-top: none;
  border-radius: 0 0 10px 10px;
}
.new-assignment.top {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  height: 90%;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
}
.new-assignment.header {
  width: 100%;
  height: 7.5%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid rgb(194, 194, 194);
